a {
  color: inherit;
  text-decoration: none;
}

.logo {
  display: block;
  height: 3rem;
  margin: 2rem auto;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row; 
}

.spread {
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.bold {
  font-family: "gotham-bold";
}

h1 {
  font-family: gotham-bold;
  font-size: 50px;
}

.items {
  max-width: 900px;
  margin: 0 auto;
}

.items > li {
  padding: 3rem;
  background: whitesmoke;
  color: #97a5aa;
  background: #fff;
  color: #000;
}

.key {
  position: relative;
  display: block;
  width: 5rem;
  line-height: 1;
  padding-right: 2rem;
  text-align: right;
  font-size: 3rem;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  letter-spacing: 0;
  font-weight: 900;
}

.key + * {
  width: 100%;
}

.key::before {
  content: "#";
  font-size: .5em;
  font-family: gotham-bold;
  vertical-align: top;
}

items > li > div > *:last-child {
  margin-top: 1rem;
}

.not {
  opacity: 0.8;
}

.pills {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.pills > * {
  border-radius: 3rem;
  padding: .5rem .8rem;
  background: #e3e5e6;
  font-size: .8rem;
}
.pills > *:not(:last-child) {
  margin-right: .5rem;
}
.pills .red { background: #eb0a6a; }
.pills .green { background: #61d17e; }
.pills .yellow { background: #f8cf4e; }

.comma-list > *:not(:last-child)::after {
  content: ', '; 
}

.dependencies a {
  white-space: nowrap;
  display: inline-block;
  color: #97a5aa;
  text-decoration: none;
}
.dependencies a i {
  vertical-align: top;
  font-size: .8em;
}

ol.deps,
ul.deps {
  margin-left: .8rem;
  padding-left: 1rem;
}

.deps li {
  margin-bottom: .6rem;
  font-size: .9rem;
}

ol.deps li {
  list-style: decimal;
}

ul.deps li {
  list-style: disc;
}

.deps li:last-of-type {
  margin-bottom: 0;
}

.deps a {
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
}
.deps a:hover {
  border-bottom-color: #000;
}

.info > div {
  margin-bottom: 1em;
}

.info > div:last-of-type {
  margin-bottom: 0;
}

@media(max-width: 600px) {
  .key {
    padding-right: 1rem;
  }
  .info .row {
    -ms-flex-direction: column;
        flex-direction: column;
    margin-left: calc(-5rem - 1rem);
  }
}


svg {
  display: block;
  margin: 0 auto;
  pointer-events: all;
}

.chord {
  fill-opacity: .6;
  stroke-opacity: 0.5;
}

.group path {
  fill-opacity: 0.8;
}

.group text {
  font-size: 16px;
  cursor: pointer;
}

.group text:hover {
  fill: #000;
  stroke: #000;
  stroke-width: 1;
}




