@font-face {
  font-family: "gotham";
  src: url("./fonts/Gotham-Book.otf") format("opentype");
}
@font-face {
  font-family: "gotham-bold";
  src: url("./fonts/Gotham-Bold.otf") format("opentype");
}

body {
  margin: 0;
  padding: 0;
  font-family: "gotham", serif;
  font-size: 18px;
  line-height: 1.6;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}


